.searchContent {
    height: 100vh;
    background-color: #000;
}

.search__container {
    background-color: #000;
    width: 100%;
    text-align: center;
    padding: 5rem 0;
}

.no-result {
    display: flex;
    justify-content: center;
    align-content: center;
    color: #f5f5f5;
    height: 100%;
    padding: 8rem;  
}

.movie {
    flex: 1 1 auto;
    display: inline-block;
    padding-right: 0.5rem;
    padding-bottom: 7rem;
}

.movie__column-poster {
    cursor: pointer;
    transition: transform 0.3s;
    -webkit-transition: transform 0.3s;
}

.movie__column-poster:hover {
    transform: scale(1.25);
}

.movie__poster {
    width: 90%;
    border-radius: 5px;
}